<template>
  <div class="userFiled">
    <userfiled-options @dialogState="dialogState"/>
    <userfiled-table @dialogState="dialogState"/>
    <add-userfiled v-if="state" :header="title" :data="data" @dialogState="dialogState"/>
  </div>
</template>

<script>
import UserfiledOptions from './controls/userfiledOptions'
import UserfiledTable from './controls/userfiledTable'
import AddUserfiled from './controls/addUserfiled'
export default {
  name: 'userFiled',
  data() {
    return {
      title:'',
      data:'',
      state: false
    }
  },
  methods: {
    dialogState(data){
      data = data || {};
      this.state = data.state;
      this.title = data.title;
      this.data = data.data;
    }
  },
  components: {
    UserfiledOptions,
    UserfiledTable,
    AddUserfiled
  }
}
</script>
 
