<template>
  <el-container>
    <el-main>
      <el-table :data="userFiledList">
        <el-table-column prop="id" label="人员ID" />
        <el-table-column prop="userName" label="姓名" />
        <el-table-column prop="nickName" label="微信昵称" />
        <el-table-column prop="address" label="地区" />
        <el-table-column prop="mobile" label="手机号码" />
        <el-table-column prop="remarks" label="备注" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="delMsg(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>

      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>确认删除该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
import { userFiledInfo } from "../../../../data/userManage";
export default {
  name: "userfiledTable",
  data() {
    return {
      userFiledList: Array(8).fill(userFiledInfo),
      dialogVisible: false,
      currentPage: 1
    };
  },
  methods: {
    handleClick() {
      var datas = {
        title:'信息编辑',
        state: true
      }
      this.$emit('dialogState', datas);
    },
    delMsg() {
      this.dialogVisible = true;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }    
  }
};
</script>
<style lang="less" scoped>
</style>